import {
    config
} from './config.js'

const base64 = require('./base64.js'); //Base64,hmac,sha1,crypto
require('./hmac.js');
require('./sha1.js');

const Crypto = require('./crypto.js');

/*
 *oss
 *@param - filePath :
 *@param - dir:
 */

const uploadFile = function(filePath, dir) {
    if (!filePath || filePath.length < 9) {
        wx.showModal({
            title: '',
            content: '',
            showCancel: false,
        })
        return;
    }
    //       + 150
    const aliyunFileKey = dir + new Date().getTime() + Math.floor(Math.random() * 150) + '.png';

    const aliyunServerURL = config.uploadImageUrl; //OSShttps
    const accessid = config.OSSAccessKeyId;
    const policyBase64 = getPolicyBase64();
    const signature = getSignature(policyBase64); //

    return new Promise((resolve, reject) => {
        wx.uploadFile({
            url: aliyunServerURL, // url
            filePath: filePath, //
            name: 'file', //file
            formData: {
                'key': aliyunFileKey,
                'policy': policyBase64,
                'OSSAccessKeyId': accessid,
                'signature': signature,
                'success_action_status': '200',
            },
            success: function(res) {
                if (res.statusCode != 200) {
                    reject(new Error(':' + JSON.stringify(res)));
                }
                resolve(aliyunServerURL + aliyunFileKey);
            },
            fail: function(err) {
                err.wxaddinfo = aliyunServerURL;
                reject(err);
            },
        })
    })
}


const uploadVideo = function(filePath, dir) {
    if (!filePath) {
        wx.showModal({
            title: '',
            content: '',
            showCancel: false,
        })
        return;
    }
    //       + 150
    const aliyunFileKey = dir + new Date().getTime() + Math.floor(Math.random() * 150) + '.mp4';

    const aliyunServerURL = config.uploadImageUrl; //OSShttps
    const accessid = config.OSSAccessKeyId;
    const policyBase64 = getPolicyBase64();
    const signature = getSignature(policyBase64); //

    return new Promise((resolve, reject) => {
        wx.uploadFile({
            url: aliyunServerURL, // url
            filePath: filePath, //
            name: 'file', //file
            formData: {
                'key': aliyunFileKey,
                'policy': policyBase64,
                'OSSAccessKeyId': accessid,
                'signature': signature,
                'success_action_status': '200',
            },
            success: function(res) {
                if (res.statusCode != 200) {
                    reject(new Error(':' + JSON.stringify(res)));
                }
                resolve(aliyunServerURL + aliyunFileKey);
            },
            fail: function(err) {
                err.wxaddinfo = aliyunServerURL;
                reject(err);
            },
        })
    })
}

const getPolicyBase64 = function() {
    let date = new Date();
    date.setHours(date.getHours() + config.timeout);
    let srcT = date.toISOString();
    const policyText = {
        "expiration": srcT, //Policypolicy 
        "conditions": [
            ["content-length-range", 0, 50 * 1024 * 1024] // ,5mb
        ]
    };

    const policyBase64 = base64.encode(JSON.stringify(policyText));
    return policyBase64;
}

const getSignature = function(policyBase64) {
    const accesskey = config.AccessKeySecret;

    const bytes = Crypto.HMAC(Crypto.SHA1, policyBase64, accesskey, {
        asBytes: true
    });
    const signature = Crypto.util.bytesToBase64(bytes);

    return signature;
}

export {
    uploadFile,
    uploadVideo
}